import { fetchRate } from 'api/rates';
import { createAction, createAsyncAction, createStandardAction, } from 'typesafe-actions';
export const openModal = createStandardAction('dialog/price_list/open')();
export const closeModal = createAction('dialog/price_list/close');
export const { request: fetchPriceListRequest, success: fetchPriceListSuccess, failure: fetchPriceListFailure, } = createAsyncAction('priceList/fetch/request', 'priceList/fetch/success', 'priceList/fetch/failure')();
export function fetchPriceList() {
    return (dispatch, getState) => {
        dispatch(fetchPriceListRequest());
        return fetchRate()
            .then(res => {
            dispatch(fetchPriceListSuccess(res.data));
        })
            .catch(err => {
            dispatch(fetchPriceListFailure(err));
        });
    };
}
