import { stateKey } from './reducer';
export const priceList = (state) => state[stateKey];
export const rates = (state) => priceList(state).rates && priceList(state).rates.data;
export const ratesPerProvider = (state) => rates(state) &&
    rates(state).reduce((acc, rate) => (Object.assign({}, acc, { [rate.provider_uuid]: Object.assign({}, acc[rate.provider_uuid], { [rate.metric]: rate.tiered_rate[0] }) })), {});
export const status = (state) => priceList(state).status;
export const error = (state) => priceList(state).error;
export const isModalOpen = (state) => priceList(state).modal.isOpen;
export const modalName = (state) => priceList(state).modal.name;
