import { getType } from 'typesafe-actions';
import { closeModal, fetchPriceListFailure, fetchPriceListRequest, fetchPriceListSuccess, openModal, } from './actions';
export const stateKey = 'priceList';
export const defaultState = {
    rates: null,
    error: null,
    status: 0 /* none */,
    modal: {
        isOpen: false,
        name: null,
    },
};
export function reducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchPriceListRequest):
            return Object.assign({}, state, { status: 1 /* inProgress */ });
        case getType(fetchPriceListSuccess):
            return Object.assign({}, state, { rates: action.payload, error: null, status: 2 /* complete */ });
        case getType(fetchPriceListFailure):
            return Object.assign({}, state, { error: action.payload, status: 2 /* complete */ });
        case getType(closeModal):
            return Object.assign({}, state, { modal: {
                    isOpen: false,
                    name: null,
                } });
        case getType(openModal):
            return Object.assign({}, state, { modal: {
                    isOpen: true,
                    name: action.payload,
                } });
        default:
            return state;
    }
}
